:root {
  --primary-text-color: #fff;
  --secondary-text-color: #000;
  --accent-color: #d41443;
  --primary-background-color: #ffb8ca;
  --secondary-background-color: #fff5f6;
  --time-duration: 250ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

* {
  outline: none;
}

body {
  font-family: DM Sans, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

a:focus, button:focus, input:focus {
  box-shadow: 0px 0px 5px 1px var(--accent-color);
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
  line-height: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body.modal-open, body.menu-open {
  overflow: hidden;
}

.container {
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1440px;
    padding-left: 115px;
    padding-right: 115px;
  }
}

.modal-menu {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  background-color: #e17992;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-menu.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal-menu__button-close {
  color: var(--primary-text-color);
  fill: currentColor;
  transition: color var(--time-duration) var(--timing-function);
  margin-left: auto;
  display: block;
}

.modal-menu__button-close:hover, .modal-menu__button-close:focus {
  color: var(--accent-color);
}

.button-close__svg {
  width: 14px;
  height: 14px;
}

.modal-menu__button-modal {
  width: 100%;
  height: 44px;
  background-color: var(--primary-text-color);
  color: var(--accent-color);
  border-radius: 22px;
  margin-top: 60px;
  font-size: 16px;
  font-weight: 700;
  display: block;
}

.modal-menu__button-modal:hover, .modal-menu__button-modal:focus {
  color: var(--primary-text-color);
  background-color: var(--accent-color);
}

.modal-nav {
  flex-direction: column;
  gap: 22px;
  margin-top: 32px;
  display: flex;
}

.modal-nav__link {
  color: var(--primary-text-color);
  font-size: 14px;
  font-weight: 700;
}

.modal-nav__link:hover, .modal-nav__link:focus {
  color: var(--accent-color);
}

.header .container {
